import React, {Component} from "react";
import {SearchBox} from "../forms/BlueFormComponents";
import DealerListItem from "./DealerListItem";

class DealerList extends Component {
    getListItems() {
        return this.props.dealers.map((dealer) => {
            return <DealerListItem  dealer={dealer} 
                                    key={dealer.id} 
                                    confirm={this.props.confirm} 
                                    resetDealerPassword={this.props.resetDealerPassword}
                                    authorizedToEdit={this.props.authorizedToEdit}
                                    authorizedToTerminate={this.props.authorizedToTerminate}/>;
        });
    }
    render() {
        return(
            <ul className="list-group">
                {this.getListItems()}
            </ul>
        );
    }
}

DealerList.propTypes = {
    dealers: React.PropTypes.arrayOf(React.PropTypes.object).isRequired,
    resetDealerPassword: React.PropTypes.func,
    confirm: React.PropTypes.func,
    authorizedToEdit: React.PropTypes.bool,
    authorizedToTerminate: React.PropTypes.bool
};

export default DealerList;


