import React, {Component} from "react";
import ManagementListItem from "../common/ManagementListItem";

class PhoneList extends Component {
    constructor(props) {
        super(props);
        this.handlePhoneRemove = this.handlePhoneRemove.bind(this);
    }
    getPhoneListItems() {
        var self = this;
        if ((typeof(this.props.phones) !== "undefined") && (this.props.phones.length > 0)) {
            return this.props.phones.map(function(phone, index) {
                return <ManagementListItem  key={phone.id} 
                                            displayString={phone.number} 
                                            item={phone}
                                            editEnabled={self.props.editEnabled}
                                            onEdit={self.props.onEdit} 
                                            onRemove={self.handlePhoneRemove}/>;
            });
        }
        return <li className="list-group-item">No Phone Numbers</li>;
    }
    handlePhoneRemove(phone) {
        this.props.showConfirm("Are you sure you want to remove this phone?", () => {this.props.onRemove(phone)} , null);
    }
    render() {
        return(
            <ul className="list-group panel-list-group">
                {this.getPhoneListItems()}
                {!this.props.editEnabled ? null : 
                    <li className="list-group-item">
                        <div className="row">
                            <div className="col-md-4 col-sm-6 col-xs-12">
                                <button className="btn btn-default btn-block" onClick={this.props.onAdd}>
                                    <i className="fa fa-plus"></i>
                                    Add Phone
                                </button>
                            </div>
                        </div>
                    </li>    
                }
            </ul>
        );
    }
}

export default PhoneList;


