import React, {Component} from "react";
import {CheckBox} from "../forms/BlueFormComponents";

class ManagementItemUsageSelection extends Component {
    constructor(props) {
        super(props);
        this.handleUsageChange = this.handleUsageChange.bind(this);
    }
    handleUsageChange(usage, trueFalse) {
        if (trueFalse === true) {
            this.props.item.usages.push(usage);
        } else {
            var indexToSplice = null;
            this.props.item.usages.forEach((value, index) => {
                if (value.id === usage.id) {
                    indexToSplice = index;
                }
            });

            if (indexToSplice !== null) {
                this.props.item.usages.splice(indexToSplice, 1);
            }
        }

        this.props.onChange(this.props.item.usages);
    }
    getUsageDisplay() {
        var self = this;
        return this.props.usages.map((usage) => {
            var defaultState = false;
            self.props.item.usages.forEach((thisUsage) => {
                if (thisUsage.id === usage.id) {
                    defaultState = true;
                }
            });
            return (
                <div className="col-md-4" key={usage.id}>
                    <CheckBox label={usage.name} onChange={(checkedState) => { self.handleUsageChange(usage, checkedState) }} defaultState={defaultState}/>
                </div>
            );
        });
    }
    render() {
        if (this.props.usages.length <= 0) {
            return null;
        }
        return (
            <div className="row">
                <div className="col-md-12">
                    <label>Usages</label>
                </div>
                {this.getUsageDisplay()}
            </div>
        );
    }
}

ManagementItemUsageSelection.propTypes = {
    usages: React.PropTypes.arrayOf(React.PropTypes.object),
    item: React.PropTypes.object.isRequired
}

export default ManagementItemUsageSelection;


