import React, {Component} from "react";
import {Button} from "../forms/BlueFormComponents";
import ManagementListItem from "../common/ManagementListItem";

class EmailList extends Component {
    constructor(props) {
        super(props);
        this.handleEmailRemove = this.handleEmailRemove.bind(this);
    }
    getEmailListItems() {
        var self = this;
        if (this.props.emails.length > 0) {
            return this.props.emails.map(function(email, index) {
                return <ManagementListItem  key={email.id} 
                                            displayString={email.address} 
                                            item={email}
                                            editEnabled={self.props.editEnabled}
                                            onEdit={self.props.onEdit} 
                                            onRemove={self.handleEmailRemove}/>;
            });
        }
        return <li className="list-group-item">No Emails</li>;
    }
    handleEmailRemove(email) {
        this.props.showConfirm("Are you sure you want to remove this email?", () => {this.props.onRemove(email)}, null);
    }
    render() {
        return (
            <ul className="list-group panel-list-group">
                {this.getEmailListItems()}
                {!this.props.editEnabled ? null : 
                    <li className="list-group-item">
                        <div className="row">
                            <div className="col-md-4 col-sm-6 col-xs-12">
                                <Button icon="plus" onClick={this.props.onAdd}>Add Email</Button>
                            </div>
                        </div>
                    </li>    
                }
            </ul>
        );
    }
}

EmailList.propTypes = {
    emails: React.PropTypes.arrayOf(React.PropTypes.object).isRequired
};

export default EmailList;


