import React, {Component} from "react";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import {SearchBox} from "../components/forms/BlueFormComponents";
import PageHeader from "../components/common/PageHeader";
import DealerList from "../components/dealer/DealerList";
import * as dealerManagementActions from "../actions/DealerManagementActions";

class DealerListPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filteredDealers: []
        };
        this.handleSearchInputChange = this.handleSearchInputChange.bind(this);
    }
    componentWillMount() {
        this.props.getDealers();
    }
    componentWillReceiveProps(nextProps) {
        this.setState({
            filteredDealers: nextProps.dealers
        });
    }
    handleSearchInputChange(searchInput) {
        var filteredDealerList = [];
        if (searchInput.length > 0) {
            const regex = new RegExp(searchInput.toUpperCase());
            this.props.dealers.forEach((dealer) => {
                if (regex.test(dealer.dealerId.toString()) || regex.test(dealer.name.toUpperCase())) {
                    filteredDealerList.push(dealer);
                }
            });
        } else {
            filteredDealerList = this.props.dealers;
        }
        this.setState({
            filteredDealers: filteredDealerList
        });
    }
    render() {
        return(
            <div className="dealers-page">
                <PageHeader>View Dealers</PageHeader>
                <div className="row">
                    <div className="col-md-4 col-sm-6 col-xs-12">
                        <SearchBox label="Search Dealers" placeholder="Search Dealers..." onChange={this.handleSearchInputChange}/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12">
                        <DealerList dealers={this.state.filteredDealers}
                                    authorizedToEdit ={true}
                                    authorizedToTerminate={true}
                                    confirm={this.props.showConfirm} 
                                    resetDealerPassword={this.props.resetPassword}/>
                    </div>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        dealers: state.dealerManagement.dealers
    }
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators(dealerManagementActions, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(DealerListPage);


