import React, {Component} from "react";
import {TextBox, Select} from "../forms/BlueFormComponents";

class DealerEditModal extends Component {
    constructor(props){
        super(props);
        this.handleDealerNameChange = this.handleDealerNameChange.bind(this);
        this.handleDealerTypeChange = this.handleDealerTypeChange.bind(this);
        this.handleSaveClick = this.handleSaveClick.bind(this);
        this.handleCancelClick = this.handleCancelClick.bind(this);
    }
    componentDidMount(){
        this.setState({
            dealer: this.props.dealer
        });
    }
    handleDealerNameChange(name){
        this.state.dealer.name = name;
        this.setState({
            dealer: this.state.dealer
        });
    }
    handleDealerTypeChange(dealerType){
        this.state.dealer.dealerTypeId = dealerType.id;
        this.setState({
            dealer: this.state.dealer
        });
    }
    handleCancelClick(event) {
        event.preventDefault();
        this.props.hideModal();
        if(typeof(this.props.cancelFunction) === "function"){
            this.props.cancelFunction();
        }
    }
    handleSaveClick(event) {
        event.preventDefault();
        this.props.hideModal();
        this.props.saveFunction(this.state.dealer);
    }
    render() {
        return (
            <form onSubmit={this.handleSaveClick} onReset={this.handleCancelClick}>
                <div className="modal-header">
                    <h4 className="modal-title" id="modalTitle" ref="dialogTitle">
                        Edit Dealer
                    </h4>
                </div>
                <div className="modal-body">
                    <div className="row">
                        <div className="col-md-12">
                            <TextBox label="Dealer Name" value={this.props.dealer.name} onChange={this.handleDealerNameChange}/>
                        </div>
                        <div className="col-md-12">
                            <Select label="Dealer Type" options={this.props.dealerTypes} value={this.props.dealer.dealerTypeId} onChange={this.handleDealerTypeChange}/>
                        </div>
                    </div>
                </div>
                <div className="modal-footer">
                    <div className="row">
                        <div className="col-md-6 col-sm-6 col-xs-12">
                            <button className="btn btn-danger btn-block" type="reset">Cancel</button>
                        </div>
                        <div className="col-md-6 col-sm-6 col-xs-12">
                            <button className="btn btn-success btn-block" type="submit">
                                <i className="fa fa-save"></i>
                                Save
                            </button>
                        </div>
                    </div>
                </div>
            </form>
        );
    }
}

DealerEditModal.propTypes = {
    dealer: React.PropTypes.object.isRequired,
    saveFunction: React.PropTypes.func.isRequired
};

export default DealerEditModal;


