import React from "react";
import {TextBox} from "../forms/BlueFormComponents";
import ManagementItemUsageSelection from "../common/ManagementItemUsageSelection";

class AddressModal extends React.Component{
    constructor(props){
        super(props);
        this.state = {address: {line1: "", usages: []}};
        this.handleSaveClick = this.handleSaveClick.bind(this);
        this.handleCancelClick = this.handleCancelClick.bind(this);
        this.handleAddressUsageChange = this.handleAddressUsageChange.bind(this);
        this.handleLine1Update = this.handleLine1Update.bind(this);
        this.handleLine2Update = this.handleLine2Update.bind(this);
        this.handleCityUpdate = this.handleCityUpdate.bind(this);
        this.handleStateProvUpdate = this.handleStateProvUpdate.bind(this);
    }
    componentDidMount() {
        if (typeof(this.props.address) !== "undefined") {
            this.setState({
                address: this.props.address
            });
        }else{
            //set default country and state
            this.setState({
                address: {
                    country: "",
                    stateProv: ""
                }
            });
        }
    }
    handleSaveClick(event) {
        event.preventDefault();
        this.props.hideModal();
        this.props.saveFunction(this.state.address);
    }
    handleCancelClick(event) {
        event.preventDefault();
        this.props.hideModal();
        if(typeof(this.props.cancelFunction) === "function"){
            this.props.cancelFunction();
        }
    }
    handleLine1Update(value){
        this.state.address.line1 = value;
        this.setState({
            address: this.state.address
        });
    }
    handleLine2Update(value){
        this.state.address.line2 = value;
        this.setState({
            address: this.state.address
        });
    }
    handleCityUpdate(value){

    }
    handleStateProvUpdate(event){

    }
    handleAddressUsageChange(usages){
        this.state.address.usages = usages;
        this.setState({
            address: this.state.address
        });
    }
    render(){
        return (
            <form onSubmit={this.handleSaveClick} onReset={this.handleCancelClick}>
                <div className="modal-header">
                    <h4 className="modal-title" id="modalTitle" ref="dialogTitle">
                        {(typeof(this.props.address) === "undefined") ? "Add Address" : "Edit Address" }
                    </h4>
                </div>
                <div className="modal-body">
                    <div className="row">
                        <div className="col-md-12">
                            <TextBox label="Line 1" value={this.state.address.line1} onChange={this.handleLine1Update}/>
                        </div>
                        <div className="col-md-12">
                            <TextBox label="Line 2" value={this.state.address.line2} onChange={this.handleLine2Update}/>
                        </div>
                        <div className="col-md-12">
                            <TextBox label="City" value={this.state.address.city} onChange={this.handleCityUpdate}/>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12">
                            <ManagementItemUsageSelection item={this.state.address} usages={this.props.usages} onChange={this.handleAddressUsageChange}/>
                        </div>
                    </div>
                </div>
                <div className="modal-footer">
                    <div className="row">
                        <div className="col-md-6 col-sm-6 col-xs-12">
                            <button className="btn btn-danger btn-block" type="reset">Cancel</button>
                        </div>
                        <div className="col-md-6 col-sm-6 col-xs-12">
                            <button className="btn btn-success btn-block" type="submit">
                                <i className="fa fa-save"></i>
                                Save
                            </button>
                        </div>
                    </div>
                </div>
            </form>
        );
            }
};

export default AddressModal;


