import React, {Component} from "react";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import ShadowBox from "../components/common/ShadowBox";
import PageHeader from "../components/common/PageHeader";
import DealerInformationList from "../components/dealer/DealerInformationList";
import DealerContextMenu from "../components/dealer/DealerContextMenu";
import EmailList from "../components/email/EmailList";
import PhoneList from "../components/phone/PhoneList";
import AddressList from "../components/address/AddressList";
import RoleList from "../components/roles/RoleList";
import PostalCodeList from "../components/postal_codes/PostalCodeList";
import * as dealerManagementActions from "../actions/DealerManagementActions";
import * as ModalTypes from "../components/modals/ModalTypes";

class DealerManagementPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            editEnabled: true
        };
        this.handleEditDealer = this.handleEditDealer.bind(this);
        this.handleAddEmail = this.handleAddEmail.bind(this);
        this.handleEditEmail = this.handleEditEmail.bind(this);
        this.handleAddPhone = this.handleAddPhone.bind(this);
        this.handleEditPhone = this.handleEditPhone.bind(this);
        this.handleAddAddress = this.handleAddAddress.bind(this);
        this.handleEditAddress = this.handleEditAddress.bind(this);
    }
    componentWillMount(){
        var targetId = parseInt(this.props.params.id);
        
        //Fetch select list options
        this.props.getOptions(targetId);

        //If we've already loaded dealer via dealer list page, don't hit api again
        const dealer = this.props.dealers.find(x => {
            return x.id === targetId;
        });
        if (typeof(dealer) !== "undefined") {
            this.props.dealerUpdated(dealer);
        //otherwise make api call to fetch dealer
        } else {
            this.props.getDealer(targetId);
        }
    }
    handleEditDealer() {
        this.props.showModal(ModalTypes.DEALER_EDIT, {
            dealer: this.props.dealer,
            dealerTypes: this.props.options.dealerTypes,
            saveFunction: this.props.updateDealer
        });
    }
    handleAddEmail() {
        this.props.showModal(ModalTypes.EMAIL,{
            usages: this.props.options.emailUsages, 
            saveFunction: this.props.createEmail
        });
    }
    handleEditEmail(email) {
        this.props.showModal(ModalTypes.EMAIL,{
            email: email, 
            usages: this.props.options.emailUsages, 
            saveFunction: this.props.updateEmail
        });
    }
    handleAddPhone() {
        this.props.showModal(ModalTypes.PHONE,{
            usages: this.props.options.phoneUsages, 
            saveFunction: this.props.createPhone
        });
    }
    handleEditPhone(phone) {
        this.props.showModal(ModalTypes.PHONE,{
            phone: phone,
            usages: this.props.options.phoneUsages,
            saveFunction: this.props.updatePhone
        });
    }
    handleAddAddress() {
        this.props.showModal(ModalTypes.ADDRESS,{
            usages: this.props.options.addressUsages,
            saveFunction: this.props.createAddress
        });
    }
    handleEditAddress(address) {
        this.props.showModal(ModalTypes.ADDRESS,{
            address: address,
            usages: this.props.options.addressUsages,
            saveFunction: this.props.updateAddress
        });
    }
    render() {
        return (
            <div className="dealer-management-page">
                <PageHeader>Manage Dealer</PageHeader>
                <div className="row">
                    <div className="col-md-8 col-md-offset-1 col-sm-12 col-xs-12">
                        <div className="row" id="basicInformation">
                            <div className="col-md-12">
                                <ShadowBox heading="Basic Information">
                                    <DealerInformationList dealer={this.props.dealer} editEnabled={true} onEdit={this.handleEditDealer}/>
                                </ShadowBox>    
                            </div>
                        </div>
                        <div className="row" id="emails">
                            <div className="col-md-12">
                                <ShadowBox heading="Emails">
                                    <EmailList  emails={this.props.emails} 
                                                editEnabled={true} 
                                                showConfirm={this.props.showConfirm}
                                                onAdd={this.handleAddEmail} 
                                                onEdit={this.handleEditEmail} 
                                                onRemove={this.props.removeEmail}/>
                                </ShadowBox>
                            </div>
                        </div>
                        <div className="row" id="phones">
                            <div className="col-md-12">
                                <ShadowBox heading="Phones">
                                    <PhoneList  phones={this.props.phones} 
                                                editEnabled={true} 
                                                showConfirm={this.props.showConfirm}
                                                onAdd={this.handleAddPhone} 
                                                onEdit={this.handleEditPhone}
                                                onRemove={this.props.removePhone}/>
                                </ShadowBox>
                            </div>    
                        </div>
                        <div className="row" id="addresses">
                            <div className="col-md-12">
                                <ShadowBox heading="Addresses">
                                    <AddressList addresses={this.props.addresses} 
                                                 editEnabled={true} 
                                                 showConfirm={this.props.showConfirm} 
                                                 onAdd={this.handleAddAddress}
                                                 onEdit={this.handleEditAddress}
                                                 onRemove={this.props.removeAddress}/>
                                </ShadowBox>
                            </div>    
                        </div>
                        <div className="row" id="permissions">
                            <div className="col-md-12">
                                <ShadowBox heading="Permissions">
                                    <RoleList roles={this.props.roles} availableRoles={this.props.options.availableRoles} onUpdate={this.props.updateRoles}/>
                                </ShadowBox>
                            </div>
                        </div>
                        <div className="row" id="postalcodes">
                            <div className="col-md-12">
                                <ShadowBox heading="Postal Codes">
                                    <PostalCodeList />
                                </ShadowBox>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-3 hidden-sm hidden-xs">
                        <DealerContextMenu />
                    </div>
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        options: state.dealerManagement.options,
        dealers: state.dealerManagement.dealers,
        dealer: state.dealerManagement.selectedDealer,
        emails: state.dealerManagement.selectedDealer.emails, //have to specify nested props this way, because redux connect function only does a shallow comparison, and won't re-render if an email changes
        phones: state.dealerManagement.selectedDealer.phones,
        addresses: state.dealerManagement.selectedDealer.addresses,
        roles: state.dealerManagement.selectedDealer.roles
    }
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators(dealerManagementActions, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(DealerManagementPage);


