import $ from "jquery";

class DealerApi {
    static getOptions(dealerId) {
        return $.ajax("/api/dealers/" + dealerId + "/options",{
            type: "GET"
        });
    }
    static getDealers() {
        return $.ajax("/api/dealers",{
            type: "GET"
        });
    }
    static getDealer(id) {
        return $.ajax("/api/dealers/" + id,{
            type: "GET"
        });
    }
    static putDealer(dealer) {
        return $.ajax("/api/dealers/" + dealer.id, {
            type: "PUT",
            dataType: "json",
            contentType: "application/json; charset=utf-8",
            data: JSON.stringify(dealer)
        });
    }
    static postDealer(dealer) {
        return $.ajax("/api/dealers", {
            type: "POST",
            dataType: "json",
            contentType: "application/json; charset=utf-8",
            data: JSON.stringify(dealer)
        });
    }
    static postEmail(dealerId, email) {
        return $.ajax("/api/dealers/" + dealerId + "/emails", {
            type: "POST",
            dataType: "json",
            contentType: "application/json; charset=utf-8",
            data: JSON.stringify(email)
        });
    }
    static putEmail(dealerId, email) {
        return $.ajax("/api/dealers/" + dealerId + "/emails/" + email.id, {
            type: "PUT",
            dataType: "json",
            contentType: "application/json; charset=utf-8",
            data: JSON.stringify(email)
        });
    }
    static deleteEmail(dealerId, emailId) {
        return $.ajax("/api/dealers/" + dealerId + "/emails/" + emailId, {
            type: "DELETE"
        });
    }
    static postPhone(dealerId, phone) {
        return $.ajax("/api/dealers/" + dealerId + "/phones", {
            type: "POST",
            dataType: "json",
            contentType: "application/json; charset=utf-8",
            data: JSON.stringify(phone)
        });
    }
    static putPhone(dealerId, phone) {
        return $.ajax("/api/dealers/" + dealerId + "/phones/" + phone.id, {
            type: "PUT",
            dataType: "json",
            contentType: "application/json; charset=utf-8",
            data: JSON.stringify(phone)
        });
    }
    static deletePhone(dealerId, phoneId) {
        return $.ajax("/api/dealers/" + dealerId + "/phones/" + phoneId, {
            type: "DELETE"
        });
    }
    static postAddress(dealerId, address) {
        return $.ajax("/api/dealers/" + dealerId + "/addresses", {
            type: "POST",
            dataType: "json",
            contentType: "application/json; charset=utf-8",
            data: JSON.stringify(address)
        });
    }
    static putAddress(dealerId, address) {
        return $.ajax("/api/dealers/" + dealerId + "/addresses/" + address.id, {
            type: "PUT",
            dataType: "json",
            contentType: "application/json; charset=utf-8",
            data: JSON.stringify(address)
        });
    }
    static deleteAddress(dealerId, addressId) {
        return $.ajax("/api/dealers/" + dealerId + "/addresses/" + addressId, {
            type: "DELETE"
        });
    }
    static putRoles(dealerId, roles){
        return $.ajax("/api/dealers/" + dealerId + "/roles",{
            type: "PUT",
            dataType: "json",
            contentType: "application/json; charset=utf-8",
            data: JSON.stringify(roles)
        });
    }
    static resetPassword(id) {
        return $.ajax("/api/dealers/" + id + "/resetpassword",{
            type:"GET"
        });
    }
}

export default DealerApi;


