import React, {Component} from "react";
import {Button} from "../forms/BlueFormComponents";
import ManagementListItem from "../common/ManagementListItem";

class AddressList extends Component {
    constructor(props) {
        super(props);
        this.handleAddressRemove = this.handleAddressRemove.bind(this);
    }
    getAddressListItems() {
        var self = this;
        if ((typeof(this.props.addresses) !== "undefined") && (this.props.addresses.length > 0)) {
            return this.props.addresses.map(function(address) {
                return <ManagementListItem  key={address.id} 
                                            displayString={address.displayString} 
                                            item={address}
                                            editEnabled={self.props.editEnabled}
                                            onEdit={self.props.onEdit} 
                                            onRemove={self.handleAddressRemove}/>;
            });
        }
        return <li className="list-group-item">No Addresses</li>;
    }
    handleAddressRemove() {
        this.props.showConfirm("Are you sure you want to remove this address?", null, null);
    }
    render() {
        return(
            <ul className="list-group panel-list-group">
                {this.getAddressListItems()}
                {!this.props.editEnabled ? null : 
                    <li className="list-group-item">
                        <div className="row">
                            <div className="col-md-4 col-sm-6 col-xs-12">
                                <Button icon="plus" onClick={this.props.onAdd}>Add Address</Button>
                            </div>
                        </div>
                    </li>    
                }
            </ul>
        );
    }
}

export default AddressList;


