import React, {Component} from "react";
import {TextBox} from "../forms/BlueFormComponents";
import ManagementItemUsageSelection from "../common/ManagementItemUsageSelection";

class PhoneModal extends Component{
    constructor(props){
        super(props);
        this.state = {
            phone: {number: "", usages: []}
        };
        this.handleSaveClick = this.handleSaveClick.bind(this);
        this.handleCancelClick = this.handleCancelClick.bind(this);
        this.handlePhoneChange = this.handlePhoneChange.bind(this);
        this.handlePhoneUsageChange = this.handlePhoneUsageChange.bind(this);
    }
    componentDidMount(){
        if(typeof(this.props.phone) !== "undefined"){
            this.setState({
                phone: this.props.phone
            });
        }
    }
    handleCancelClick(event) {
        event.preventDefault();
        this.props.hideModal();
        if(typeof(this.props.cancelFunction) === "function"){
            this.props.cancelFunction();
        }
    }
    handleSaveClick(event) {
        event.preventDefault();
        this.props.hideModal();
        this.props.saveFunction(this.state.phone);
    }
    handlePhoneChange(value) {
        this.state.phone.number = value;
        this.setState({
            phone: this.state.phone
        });
    }
    handlePhoneUsageChange(usages){
        this.state.phone.usages = usages;
        this.setState({
            phone: this.state.phone
        });
    }
    render(){
        return (
            <form onSubmit={this.handleSaveClick} onReset={this.handleCancelClick}>
                <div className="modal-header">
                    <h4 className="modal-title" id="modalTitle" ref="dialogTitle">
                        {typeof(this.props.phone) === "undefined" ? "Add Phone" : "Edit Phone"}
                    </h4>
                </div>
                <div className="modal-body">
                    <div className="row">
                        <div className="col-md-12">
                            <TextBox label="Phone Number" value={this.state.phone.number} onChange={this.handlePhoneChange}/>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12">
                            <ManagementItemUsageSelection item={this.state.phone} usages={this.props.usages} onChange={this.handlePhoneUsageChange}/>
                        </div>
                    </div>
                </div>
                <div className="modal-footer">
                    <div className="row">
                        <div className="col-md-6 col-sm-6 col-xs-12">
                            <button className="btn btn-danger btn-block" type="reset">Cancel</button>
                        </div>
                        <div className="col-md-6 col-sm-6 col-xs-12">
                            <button className="btn btn-success btn-block" type="submit">
                                <i className="fa fa-save"></i>
                                Save
                            </button>
                        </div>
                    </div>
                </div>
            </form>
        );
    }
};

export default PhoneModal;


