import React from "react";
import {Route, IndexRoute} from "react-router";
import DealerListPage from "../containers/DealerListPage";
import DealerManagementPage from "../containers/DealerManagementPage";
import DealerTerminationPage from "../containers/DealerTerminationPage";
import EmployeeListPage from "../containers/EmployeeListPage";
import EmployeeManagementPage from "../containers/EmployeeManagementPage";
import * as permissions from "../permissions/CorePermissions";

export default function(orchardAuthorize) {
    function authorize(permission, replace) {
        if (!orchardAuthorize(permission)) {
            replace("/users/account/accessdenied");
        } 
    }
    return (
        <Route path="core">
            <IndexRoute component={DealerListPage} onEnter={(nextState, replace) => {authorize(permissions.VIEW_DEALERS, replace)}}/>
            <Route path="dealers" component={DealerListPage} onEnter={(nextState, replace) => {authorize(permissions.VIEW_DEALERS, replace)}}/>
            <Route path="dealers/:id" component={DealerManagementPage} onEnter={(nextState, replace) => {authorize(permissions.VIEW_DEALERS, replace)}}/>
            <Route path="dealers/:id/terminate" component={DealerTerminationPage} onEnter={(nextState, replace) => {authorize(permissions.DELETE_DEALER, replace)}}/>
            <Route path="employees" component={EmployeeListPage}  onEnter={(nextState, replace) => {authorize(permissions.VIEW_EMPLOYEES, replace)}}/>
            <Route path="employees/:id" component={EmployeeManagementPage} onEnter={(nextState, replace) => {authorize(permissions.EDIT_EMPLOYEES, replace)}}/>
        </Route>
    );
}


