import React, {Component} from "react";

class DealerInformationList extends Component {
    constructor(props) {
        super(props);
        this.handleEditClick = this.handleEditClick.bind(this);
    }
    handleEditClick() {
        this.props.onEdit(this.props.dealer);
    }
    render() {
        return(
            <ul className="list-group panel-list-group">
                {this.props.editEnabled ? 
                    <li className="list-group-item">
                        <div className="row">
                            <div className="col-md-4 col-md-offset-8 col-sm-6 col-sm-offset-6 col-xs-12">
                                <button className="btn btn-default btn-block" onClick={this.handleEditClick}>
                                    <i className="fa fa-edit"></i>
                                    Edit
                                </button>
                            </div>
                        </div>
                    </li> : null}
                <li className="list-group-item">
                    <label>Dealer Name</label>
                    <span>{this.props.dealer.name}</span>
                </li>
                <li className="list-group-item">
                    <label>Dealer Owner</label>
                    <span>{this.props.dealer.ownerName + " (" + this.props.dealer.ownerUserName + ")" }</span>
                </li>
                <li className="list-group-item">
                    <label>Dealer ID</label>
                    <span>{this.props.dealer.dealerId}</span>
                </li>
                <li className="list-group-item">
                    <label>Dealer Type</label>
                    <span>{this.props.dealer.dealerType}</span>
                </li>
                <li className="list-group-item">
                    <label>Distributor</label>
                    <span>{this.props.dealer.distributor}</span>
                </li>
            </ul>
        );
    }
}

DealerInformationList.propTypes = {
    dealer: React.PropTypes.object.isRequired
};

export default DealerInformationList;


