import * as types from "../action_types/DealerActionTypes";
import DealerApi from "../api/DealerApi";
import ModalActions from "../../../RadioSystems.React/ReactBase/actions/ModalActions";


export function getOptions(dealerId){
    return (dispatch) => {
        DealerApi.getOptions(dealerId).done(options => {
            dispatch(optionsLoaded(options));
        });
    }
}

export function optionsLoaded(options){
    return { type: types.OPTIONS_LOADED, options };
}

export function createDealer(dealer){
        
}

export function getDealers(){
    return (dispatch) => {
        return DealerApi.getDealers().done(dealers => {
            dispatch(dealersUpdated(dealers));
        }).fail(error => {
            //dispatch(ErrorActions.apiError(error));
        });
    }
} 

export function getDealer(id){
    return (dispatch) => {
        return DealerApi.getDealer(id)
            .done(dealer => {
                dispatch(dealerUpdated(dealer));
            }).fail(error => {

            });
    }
}

export function dealersUpdated(dealers){
    return { type: types.DEALERS_UPDATED, dealers };
}

export function dealerUpdated(dealer){
    return { type: types.DEALER_UPDATED, dealer };
}

export function updateDealer(dealer){
    return (dispatch) => {
        return DealerApi.putDealer(dealer).done(dealer => {
            dispatch(dealerUpdated(dealer));
            dispatch(ModalActions.showAlert("Dealer Updated!"));
        }).fail(error => {console.error(error)});
    }       
}

export function createEmail(email){
    return (dispatch, getState) => {
        var dealerId = getState().dealerManagement.selectedDealer.id;
        return DealerApi.postEmail(dealerId, email).done(emails => {
            dispatch(emailsUpdated(emails));
            dispatch(ModalActions.showAlert("Email Added!"));
        });
    }
}

export function updateEmail(email){
    return (dispatch, getState) => {
        var dealerId = getState().dealerManagement.selectedDealer.id;
        return DealerApi.putEmail(dealerId, email).done(emails => {
            dispatch(emailsUpdated(emails));
            dispatch(ModalActions.showAlert("Email Updated!"));
        }).fail(error => {
            console.error(error);
        });
    }
}

export function removeEmail(email){
    return (dispatch, getState) => {
        var dealerId = getState().dealerManagement.selectedDealer.id;
        return DealerApi.deleteEmail(dealerId, email.id).done(emails => {
            dispatch(emailsUpdated(emails));
            dispatch(ModalActions.showAlert("Email Removed!"));
        });
    }
}

export function emailsUpdated(emails){
    return { type: types.EMAILS_UPDATED, emails };
}

