export function resetPassword(){
    return (dispatch, getState) => {
        const dealerId = getState().dealerManagement.selectedDealer.id;
        return DealerApi.resetPassword(dealerId).done(() => {
            dispatch(ModalActions.showAlert("Dealer password successfully reset."));
        }).fail(error => {

        });
    }
}

export function updateRoles(roles){
    return (dispatch, getState) => {
        var dealerId = getState().dealerManagement.selectedDealer.id;
        DealerApi.putRoles(dealerId, roles).done(updatedRoles => {
            dispatch(rolesUpdated(updatedRoles));
            dispatch(ModalActions.showAlert("Roles Updated"));
        }).fail(error => {
            console.error(error);
        });
    }
}

export function rolesUpdated(roles){
    return {type: types.ROLES_UPDATED, roles};
}



