import React from "react";
import {TextBox} from "../forms/BlueFormComponents";
import ManagementItemUsageSelection from "../common/ManagementItemUsageSelection";

/* 
    EmailModal Component     
*/

class EmailModal extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            email: {address: "", usages: []}
        };
        this.handleSaveClick = this.handleSaveClick.bind(this);
        this.handleCancelClick = this.handleCancelClick.bind(this);
        this.handleEmailChange = this.handleEmailChange.bind(this);
        this.handleEmailUsageChange = this.handleEmailUsageChange.bind(this);
    }
    componentDidMount() {
        if (typeof(this.props.email) !== "undefined") {
            this.setState({
                email: this.props.email
            });
        }
    }
    handleCancelClick(event) {
        event.preventDefault();
        this.props.hideModal();
        if(typeof(this.props.cancelFunction) === "function"){
            this.props.cancelFunction();
        }
    }
    handleSaveClick(event) {
        event.preventDefault();
        this.props.hideModal();
        this.props.saveFunction(this.state.email);
    }
    handleEmailChange(value) {
        this.state.email.address = value;
        this.setState({
            email: this.state.email
        });
    }
    handleEmailUsageChange(usages) {
        this.state.email.usages = usages;
        this.setState({
            email: this.state.email
        });
    }
    render() {
        return (
            <form onSubmit={this.handleSaveClick} onReset={this.handleCancelClick}>
                <div className="modal-header">
                    <h3 className="modal-title" id="modalTitle" ref="dialogTitle">
                        {typeof(this.props.email) === "undefined" ? "Add Email" : "Edit Email"}
                    </h3>
                </div>
                <div className="modal-body">
                    <div className="row">
                        <div className="col-md-12">
                            <TextBox label="Email Address" value={this.state.email.address} onChange={this.handleEmailChange}/>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12">
                            <ManagementItemUsageSelection item={this.state.email} usages={this.props.usages} onChange={this.handleEmailUsageChange}/>
                        </div>
                    </div>
                </div>
                <div className="modal-footer">
                    <div className="row">
                        <div className="col-md-6 col-sm-6 col-xs-12">
                            <button className="btn btn-danger btn-block" type="reset">Cancel</button>
                        </div>
                        <div className="col-md-6 col-sm-6 col-xs-12">
                            <button className="btn btn-success btn-block" type="submit">
                                <i className="fa fa-save"></i>
                                Save
                            </button>
                        </div>
                    </div>
                </div>
            </form>
        );
    }
};

EmailModal.propTypes = {
    saveFunction: React.PropTypes.func.isRequired,
    cancelFunction: React.PropTypes.func,
    email: React.PropTypes.object
}

export default EmailModal;


