import React, {Component} from "react";

class PageHeader extends Component {
    render() {
        return(
            <div className="row">
                <div className="col-md-12">
                    <div className="title">
                        <h1>{this.props.children}</h1>
                    </div>
                </div>
            </div>
        );
    }
}

export default PageHeader;


