import React, {Component} from "react";
import {findDOMNode} from "react-dom";

class Button extends Component{
    constructor(props){
        super(props);
        this.handleButtonClick = this.handleButtonClick.bind(this);
    }
    handleButtonClick(){
        this.props.onClick();
    }
    render(){
        return (
            <button className={"btn btn-block btn-" + this.props.type} onClick={this.handleButtonClick}>
                {typeof(this.props.icon) !== "undefined" ? <i className={"fa fa-" + this.props.icon}></i> : null}
                {this.props.children}
            </button>
        );
    }
}
Button.defaultProps = {
    type: "default"
}

class TextBox extends Component {
    constructor(props) {
        super(props);
        this.handleValueChange = this.handleValueChange.bind(this);
    }
    handleValueChange(event) {
        this.props.onChange(event.target.value);
    }
    render() {
        let requiredIndicator = null;

        if (this.props.required && !this.props.disabled) {
            requiredIndicator = <i className="fa fa-asterisk required"></i>
        }

        return (
            <div className="form-group">
                <label ref="label">{this.props.label}</label>
                {requiredIndicator}
                <input type={this.props.type}
                        className="form-control"
                        placeholder={this.props.placeholder}
                        value={this.props.value || ""}
                        onBlur={this.props.onBlur}
                        onChange={this.handleValueChange}
                        disabled={this.props.disabled}
                        ref="input" />
            </div>
        );
    }
}
TextBox.defaultProps = {
    disabled: false,
    required: false,
    placeholder: "",
    label: "",
    value: "",
    onBlur: function() {},
    onChange: function() {}
}

class CheckBox extends Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
    }
    handleChange(event) {
        var checkedState = findDOMNode(this.refs.checkbox).checked;
        this.props.onChange(checkedState);
    }
    render() {
        return(
            <div className="form-group">
                <div className="checkbox">
                    <label>
                        <input type="checkbox" onChange={this.handleChange} ref="checkbox" checked={this.props.defaultState} disabled={this.props.disabled}/>
                        {this.props.label}
                    </label>
                </div>
            </div>
        );
    }
}

class SearchBox extends Component{
    constructor(props) {
        super(props);
        this.handleSearchInputChange = this.handleSearchInputChange.bind(this);
    }
    handleSearchInputChange(event) {
        this.props.onChange(event.target.value);
    }
    render(){
        return (
            <div className="form-group">
                {(typeof(this.props.label) === "undefined") ? null : <label>{this.props.label}</label> }
                <div className="input-group">
                    <input type="text" className="form-control" placeholder={this.props.placeholder} onChange={this.handleSearchInputChange} />
                    <span className="input-group-btn">
                        <button className="btn btn-flat">
                            <i className="fa fa-search"></i>
                        </button>
                    </span>
                </div>
            </div>         
        );
    }
};

class Select extends Component{
    constructor(props){
        super(props);
        this.handleSelectedValueChange = this.handleSelectedValueChange.bind(this);
    }
    handleSelectedValueChange(event) {
        var selection = {
            id: parseInt(event.target.value),
            name: event.target.options[event.target.selectedIndex].text
        };
        this.props.onChange(selection);
    }
    getOptions() {
        var options = this.props.options.map((option, index) => {
            if ((typeof (option.id) != "undefined") && (typeof (option.name) != "undefined")) {
                return (
                    <option value={option.id} key={option.id}>{option.name}</option>
                );
            }
            else {
                return (
                    <option key={option} value={option}>{option}</option>
                );
            }
        });

        return options;
    }
    render() {
        var requiredIndicator = null;

        if (this.props.required && !this.props.disabled) {
            requiredIndicator = <i className="fa fa-asterisk required"></i>
        }

        return (
            <div className="form-group">
                <label>{this.props.label}</label>
                {requiredIndicator}
                <select className="form-control" 
                        onChange={this.handleSelectedValueChange} 
                        disabled={this.props.disabled}
                        value={this.props.value || ""}
                        ref="select">
                    {this.getOptions()}
                </select>
            </div>
        );
    }
}
Select.defaultProps = {
    disabled: false,
    label: "",
    options: [],
    value: ""
};
Select.propTypes = {
    options: React.PropTypes.array.isRequired,
    required: React.PropTypes.bool
};

class MultiSelect extends Component{
    constructor(props){
        super(props);
        this.handleSelectionChange = this.handleSelectionChange.bind(this);
    }
    getSelectedOptions(){
        var options = findDOMNode(this.refs.multiSelectElement).options;
        var selectedOptions = [];

        for (var i = 0; i < options.length; i++) {
            if (options[i].selected) {
                selectedOptions.push({ name: options[i].text, id: parseInt(options[i].value) });
            }
        }

        return selectedOptions;
    }
    handleSelectionChange () {
        var selections = this.getSelectedOptions();
        this.props.onChange(selections);
    }
    getOptions () {
        var options = this.props.options.map(function(option, index){
            return(<option value={option.id} key={index}>{option.name}</option>)
        });

        return options;
    }
    render () {
        return(
            <div className="form-group">
                <label>{this.props.label}</label>
                <select ref="multiSelectElement"
                    size={this.props.size} 
                    className="form-control" 
                    multiple="multiple"  
                    onChange={this.handleSelectionChange}>

                    {this.getOptions()}
                </select>
            </div>
        );
    }
}
MultiSelect.defaultProps = {
    label: "",
    options: [],
    size: 5
};
MultiSelect.propTypes = {
    options: React.PropTypes.array.isRequired,
    size: React.PropTypes.number,
    label: React.PropTypes.string
};

export {Button, TextBox, CheckBox, SearchBox, Select, MultiSelect};


