import objectAssign from "object-assign";
import * as types from "../action_types/DealerActionTypes";

const dealerActionsHandler = {
    [types.OPTIONS_LOADED](state, action) {
        const newState = objectAssign({}, state);
        newState.options = action.options;
        return newState;
    },
    [types.DEALERS_UPDATED](state, action) {
        const newState = objectAssign({}, state);
        newState.dealers = action.dealers;
        return newState;
    },
    [types.DEALER_UPDATED](state, action) {
        const newState = objectAssign({}, state);
        newState.selectedDealer = action.dealer;
        return newState;
    },
    [types.EMAILS_UPDATED](state, action) {
        const newState = objectAssign({}, state);
        newState.selectedDealer.emails = action.emails;
        return newState;
    },
    [types.PHONES_UPDATED](state, action){
        const newState = objectAssign({}, state);
        newState.selectedDealer.phones = action.phones;
        return newState;
    },
    [types.ADDRESSES_UPDATED](state, action){
        const newState = objectAssign({}, state);
        newState.selectedDealer.addresses = action.addresses;
        return newState;
    }
}

export default dealerActionsHandler;


