import React, {Component} from "react";
import {Button} from "../forms/BlueFormComponents";

class ManagementListItem extends Component {
    constructor(props) {
        super(props);
        this.handleRemoveClick = this.handleRemoveClick.bind(this);
        this.handleEditClick = this.handleEditClick.bind(this);
    }
    handleEditClick() {
        this.props.onEdit(this.props.item);
    }
    handleRemoveClick() {
        this.props.onRemove(this.props.item);
    }
    getEditButton() {
        if (this.props.editEnabled) {
            return (
                <div className="col-md-3 col-sm-3 col-xs-6">
                    <Button icon="edit" onClick={this.handleEditClick}>Edit</Button>
                </div>
            );
        }
        return null;
    }
    getRemoveButton() {
        if (this.props.editEnabled && !this.props.item.isDefault) {
            return(
                <div className="col-md-3 col-sm-3 col-xs-6">
                    <Button type="danger" icon="remove" onClick={this.handleRemoveClick}>Remove</Button>
                </div>
            );
        }
        return null;
    }
    getDefaultBadge() {
        return this.props.item.isDefault ? <span className="badge default">Default</span> : null;
    }
    getUsageBadges() {
        return this.props.item.usages.map(usage => {
            return <span className="badge" key={usage.id}>{usage.name}</span>;
        });
    }
    render() {
        return(
            <li className="list-group-item">
                <div className="row">
                    <div className={this.props.item.isDefault ? "col-md-9 col-sm-9 col-xs-12" : "col-md-6 col-sm-6 col-xs-12"}>
                        <span>{this.props.displayString}</span>
                    </div>
                    {this.getEditButton()}
                    {this.getRemoveButton()}
                    <div className="col-md-12">
                        {this.getDefaultBadge()}{this.getUsageBadges()}
                    </div>
                </div>
            </li>    
        );
    }
}

ManagementListItem.propTypes = {
    item: React.PropTypes.object.isRequired,
    displayString: React.PropTypes.string.isRequired,
    editEnabled: React.PropTypes.bool,
    onEdit: React.PropTypes.func,
    onRemove: React.PropTypes.func
};

export default ManagementListItem;


