import React, {Component} from "react";
import {Link} from "react-router";

class DealerListItem extends Component {
    constructor(props) {
        super(props);
        this.handleResetPasswordClick = this.handleResetPasswordClick.bind(this);
        this.resetDealerPassword = this.resetDealerPassword.bind(this);
    }
    resetDealerPassword() {
        this.props.resetDealerPassword(this.props.dealer.id);
    }
    handleResetPasswordClick() {
        this.props.confirm("Are you sure you want to reset this dealer's password?", this.resetDealerPassword);
    }
    getResetPasswordLink() {
        if (this.props.authorizedToEdit) {
            return <span><span>|</span><a href="#" onClick={this.handleResetPasswordClick}>Reset Password</a></span>;
        }
        return null;
    }
    getTerminateDealerLink() {
        if (this.props.authorizedToEdit) {
            return <span><span>|</span><Link to={"/react/core/dealers/" + this.props.dealer.id + "/terminate"}>Terminate</Link></span>;
        }
        return null;
    }
    render() {
        return(
            <li className="list-group-item">
                <div className="row">
                    <div className="col-md-8 col-sm-12 col-xs-12">
                        <h3>{this.props.dealer.name}</h3>
                        <span>({"Dealer Id: " + this.props.dealer.dealerId})</span>
                    </div>
                    <div className="col-md-4 col-sm-12 col-xs-12">
                        <div className="inline">
                            <Link to={"/react/core/dealers/" + this.props.dealer.id}>View</Link>
                            {this.getResetPasswordLink()}
                            {this.getTerminateDealerLink()}
                        </div>
                    </div>
                </div>
            </li>
        );
    }
}

DealerListItem.propTypes = {
    resetDealerPassword: React.PropTypes.func,
    confirm: React.PropTypes.func,
    authorizedToEdit: React.PropTypes.bool,
    authorizedToTerminate: React.PropTypes.bool
};

export default DealerListItem;


