import React, {Component} from "react";
import {CheckBox, Button} from "../forms/BlueFormComponents";

class RoleList extends Component{
    constructor(props){
        super(props);
        this.handleRoleChange = this.handleRoleChange.bind(this);
        this.handleSaveClick = this.handleSaveClick.bind(this);
        this.state = {
            availableRoles: [],
            roles: []
        }
    }
    componentWillReceiveProps(nextProps){
        this.setState({
            availableRoles: nextProps.availableRoles,
            roles: nextProps.roles
        });
    }
    addRole(role){
        this.state.roles.push(role.id);
        this.setState({
            roles: this.state.roles
        });
    }
    removeRole(role){
        const index = this.state.roles.indexOf(role.id);
        this.state.roles.splice(index, 1);
        this.setState({
            roles: this.state.roles
        });
    }
    handleRoleChange(checkedState, role){
        (checkedState === true) ? this.addRole(role) : this.removeRole(role);
    }
    handleSaveClick(){
        this.props.onUpdate(this.state.roles)
    }
    render(){
        return(
            <div>
                <div className="row">
                    <div className="col-md-12">
                        <ul className="list-group">
                            {
                                this.state.availableRoles.map(role => {
                                    var checkedState = this.state.roles.indexOf(role.id) === -1 ? false : true;
                                    return (
                                        <li className="list-group-item" key={role.id}>
                                            <CheckBox label={role.name} onChange={(checkedState) => {this.handleRoleChange(checkedState, role)}} defaultState={checkedState}/>
                                        </li>
                                    );
                                })
                            }
                        </ul>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-4 col-sm5 col-xs-12">
                        <Button onClick={this.handleSaveClick} type="success" icon="save">Save Roles</Button>
                    </div>
                </div>
            </div>
        )
    }
}

RoleList.propTypes = {
    availableRoles: React.PropTypes.array,
    roles: React.PropTypes.array.isRequired,
    onUpdate: React.PropTypes.func
};

export default RoleList;


